<template>
  <div style="height:100%;background:#161b21">
    <!-- 无钱包提示 -->
    <section>
      <div class="no-title"><img :src="noneicon" />
      <span>{{$t("message.walletManage.noDetectionWallet")}}</span>
    </div>
    <div class="no-toast"><img :src="noWalletTitle" style="width:0.3rem;height:0.3rem;" />
      <span>{{getNoWalletTip}}</span> <br> {{$t("message.walletManage.addWalletFirst")}}
    </div>
    <!-- 导入钱包btn -->
    <van-row class="add-wallet">
      <van-col class="add-top">
        <div style="padding: 0.3rem 0.2rem 0.2rem;" @click="addWallet">
          <img :src="importWallet" />
          <span class="add-title base-font">{{$t("message.walletManage.importWallet")}}</span>
        </div>
      </van-col>
    </van-row>
    </section>
  </div>
</template>

<script>
import noneicon from "@/images/asset_nowallet.png";
import noWalletTitle from "@/images/asset_nowallet_tip.png";
import importWallet from "@/images/import_wallet.png";
export default {
  name: "noWallet",
  data() {
    return {
      noneicon,
      noWalletTitle,
      importWallet
    };
  },
  computed: {
    getNoWalletTip() {
      return this.$t("message.walletManage.mustHaveSwtc");
    }
    // noWallet() {
    //   return {
    //     width: screen.width * window.devicePixelRatio + "px",
    //     height: screen.height * window.devicePixelRatio + "px",
    //     background: "#161b21"
    //   };
    // }
  },
  methods: {
    addWallet() {
      this.$router.push({ path: "/importWallet" });
    }
  }
};
</script>
<style lang="scss" scoped>
.add-wallet {
  .add-top {
    text-align: center;
    margin: 0.2rem;
    border: 0.02rem dashed #38434f;
    height: 1rem;
    width: 95%;
    cursor: pointer;
  }
  img {
    line-height: 0.4rem;
    height: 0.4rem;
  }
  .add-title {
    position: relative;
    top: -0.1rem;
    color: #e6e6e6;
    padding-left: 0.26rem;
    line-height: 0.4rem;
  }
}
.no-title {
  color: #ff0000;
  text-align: center;
  padding-top: 1.2rem;

  img {
    height: 0.28rem;
  }
  span {
    padding-left: 0.1rem;
    position: relative;
    top: -0.04rem;
  }
}
.no-toast {
  margin: 0 0.4rem;
  color: #b9cadd;
  text-align: center;
  padding-top: 0.2rem;
  padding-bottom: 0.8rem;
  span {
    color: #f37028;
  }
}
</style>



