<template>
  <div class="wrap">
    <div class="top">
      <img src="@/images/language_left.png"  @click="goback"> 
      <span>{{$t("message.walletManage.myWallet")}}</span>
    </div>
    <section v-show="haveJcWallet" style="width:100%;height:100%;padding-top:20%;background: #161b21;">
      <div style="color:#fff;font-size:0.28rem">
        <div style="margin-bottom:0.2rem;">{{$t("message.walletManage.yourWalletAddress")}}</div> 
        <div>{{walletAddress}}</div>
      </div>
     <div class="button">
       <button @click="clearCache">{{$t("message.walletManage.clearWallet")}}</button>
     </div>
    </section>
    <noWallet v-if="!haveJcWallet"></noWallet>
  </div>
</template>
<script>
import noWallet from "./noWallet";
import Lockr from "lockr";
export default {
  name: "walletManage",
  components: { noWallet },
  data() {
    return {
      walletAddress: ""
    };
  },
  computed: {
    swtAddress() {
      return this.$store.getters.swtAddress;
    },
    haveJcWallet() {
      if (!this.$store.getters.swtAddress) {
        this.walletAddress = this.$t("message.walletManage.importWalletFirst");
        return false;
      } else {
        this.walletAddress = this.$store.getters.swtAddress;
        return true;
      }
    }
  },
  methods: {
    goback() {
      this.$router.go(-1);
    },
    clearCache() {
      Lockr.set(
        "4085118690b6b24a58e8b9a2e26a15a31f2dfbd9e6280752a04af70e3a5389cc",
        null
      );
      this.$store.dispatch("updateJCWallet", null);
    }
  }
};
</script>
<style lang="scss" scoped>
.add-top {
  text-align: center;
  margin: 0.2rem;
  border: 0.02rem dashed #38434f;
  height: 1rem;
  width: 95%;
  cursor: pointer;
}

.warp {
  width: 100%;
  height: 100%;
}
.button {
  padding: 0.2rem 0.42rem 0 0.42rem;
  button {
    width: 100%;
    height: 0.72rem;
    background-color: #389be6;
    margin: 0.2rem 0;
    color: #fff;
    border: none;
    font-size: 0.26rem;
    border-radius: 0.1rem;
  }
}
.top {
  width: 100%;
  height: 0.88rem;
  text-align: center;
  line-height: 0.8rem;
  font-size: 0.28rem;
  color: #fff;
  background-color: #29313e;
  position: absolute;
  img {
    width: 0.24rem;
    height: 0.36rem;
    position: absolute;
    left: 0.2rem;
    top: 0.24rem;
  }
}
</style>



